import React, {Component} from 'react';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid'
import {theme} from '../../theme';
import {MuiThemeProvider} from '@material-ui/core/styles'
import OutlinedButton from '../OutlineButton';
import deepPurple from '@material-ui/core/colors/deepPurple';
import SweetAlert from 'react-bootstrap-sweetalert';

class Header extends Component {

    constructor(props){
        super(props);

        this.state = {
            show: false
        }
    }

    renderAlert() {

        return  (
            <SweetAlert warning title={"Coming Soon"} onConfirm={this.changeState} confirmBtnBsStyle="primary">
                This Feature is Coming Soon!
            </SweetAlert>
        )

    }

    changeState = () => {
        this.setState({show: !this.state.show});
    };

    render(){
        return(
            <MuiThemeProvider theme={theme}>
                <AppBar color={"primary"} style={{background: deepPurple[500]}}>
                    <Toolbar>
                        <Grid container spacing={0}>
                            <Grid item xs style={{display: 'flex', justifyContent: 'flex-end', alignItems: 'center'}}>
                                <Typography variant={"body1"} style={{color: '#fff', fontFamily: 'Raleway, sans-serif', fontSize: 30}}>
                                    True Saviour
                                </Typography>
                            </Grid>
                            <Grid item xs style={{display: 'flex', justifyContent: 'flex-end'}}>
                                <OutlinedButton onClick={this.changeState}>
                                    Login
                                </OutlinedButton>
                                {this.state.show && this.renderAlert()}
                            </Grid>
                        </Grid>
                    </Toolbar>
                </AppBar>
            </MuiThemeProvider>
        )
    }

}

export default Header;



