import React, {Component} from 'react';
import {withStyles} from '@material-ui/core/styles';
import Header from '../Components/Common/Header';
import Footer from '../Components/Common/Footer';
import Jumbotron from '../Components/Jumbotron';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Divider from '@material-ui/core/Divider';
import Button from '@material-ui/core/Button';
import Paper from "@material-ui/core/Paper";
import SweetAlert from 'react-bootstrap-sweetalert';
const styles = theme => ({
    toolbar: theme.mixins.toolbar,
    leftTitle: {
        fontFamily: 'Raleway, sans-serif',
        paddingLeft: '0.5em',
        textAlign: 'center',
        fontSize: 60
    },
    rootImage: {
        minHeight: '100%',
        background: "url('/assets/images/dandelion1.png')",
        backgroundPosition: 'center',
        backgroundSize:  'contain',
        backgroundRepeat: "no-repeat",
        width: "100%",
        transform: "scaleX(-1)",
        [theme.breakpoints.down('sm')]:{
            display: 'none'
        }
    },
    saviourImage: {
        minHeight: '100%',
        background: "url('/assets/images/saviour.png')",
        backgroundPosition: 'center',
        backgroundSize:  'contain',
        backgroundRepeat: "no-repeat",
        width: "100%",
        [theme.breakpoints.down('sm')]:{
            display: 'none'
        }
    },
    image: {
        height: '100%',
        width: '100%'
    },
    leftSide: {
        textAlign: 'center'
    },
    featureCard: {
        background: 'rgba(255,255,255,0)',
        transition: '200ms ease-in',
        padding: 10,
        boxShadow: '4px 4px rgba(5,5,5,0)',
        '&:hover': {
            boxShadow: '4px 4px rgba(5,5,5,0.7)',
            background: 'rgba(255,255,255,0.3)',
            transition: '200ms ease-in'
        }
    },
    featureCardImage: {
        display: 'block',
        margin: 'auto',
        height: 100,
        width: 100
    },
    rest: {
        paddingTop: 10,
        background: 'linear-gradient(360deg, #7e57c2 30%, rgba(255,109,0,0.7) 80%)',
        width: '100vw',
        overflowX: 'hidden'
    },
    typography: {
        fontFamily: 'Raleway, sans-serif',
        textAlign: 'center'
    }
});

class HomeContainer extends Component {

    constructor(props){
        super(props);

        this.state = {
            show: false
        }
    }

    renderAlert() {

        return  (
            <SweetAlert warning title={"Coming Soon"} onConfirm={this.changeState} confirmBtnBsStyle="primary">
                This Feature is Coming Soon!
            </SweetAlert>
        )

    }

    changeState = () => {
        this.setState({show: !this.state.show});
    };

    render(){
        const {classes} = this.props;
        return(
            <div>
                {this.state.show && this.renderAlert()}
                <Header/>
                <Jumbotron>
                    <Grid container spacing={0}>
                        <Grid item md className={classes.leftSide}>
                            <Grid container spacing={0}>
                                <Grid item xs>
                                    <div className={classes.toolbar}/>
                                    <Typography className={classes.leftTitle} variant={"display1"} gutterBottom>
                                        You Deserve To Heal
                                    </Typography>
                                    <Divider style={{width: '50%',position: 'relative', left: '25%', height: 2}}/>
                                </Grid>
                            </Grid>
                            <Grid container spacing={0}>
                                <Grid item xs>
                                    <div className={classes.toolbar}/>
                                    <Typography variant={"display3"} gutterBottom style={{fontFamily: 'Raleway, sans-serif'}}>
                                        True Saviour
                                    </Typography>
                                    <Typography variant={"title"} gutterBottom>
                                        Fostering Mental Wellness through Counselling and Emotional Support
                                    </Typography>
                                </Grid>
                            </Grid>
                            <Grid container spacing={8}>
                                <Grid item xs={2}/>
                                <Grid item xs={4}>
                                    <div className={classes.toolbar}/>
                                    <Button variant={"extendedFab"} color={"primary"} onClick={this.changeState}>
                                        Find A Counsellor
                                    </Button>
                                </Grid>
                                <Grid item xs={4}>
                                    <div className={classes.toolbar}/>
                                    <Button variant={"extendedFab"} color="secondary" onClick={this.changeState}>
                                        Book An Appointment
                                    </Button>
                                </Grid>
                                <Grid item xs={2}/>
                            </Grid>
                        </Grid>
                        <Grid item md>
                            <div className={classes.rootImage}>
                                <img src="/assets/images/placeholder.png" height="100%" width="100%" alt="Girl with Dandelion"/>
                            </div>
                        </Grid>
                    </Grid>
                </Jumbotron>
                <div className={classes.rest}>
                    <Divider/>
                    {/* Features Section */}
                    <section>
                        <Grid container spacing={8}>
                            <Grid item sm={3}>
                                <Paper className={classes.featureCard}>
                                    <img className={classes.featureCardImage} src="/assets/images/artificial-intelligence.png" alt="AI"/>
                                    <Typography variant={"body1"} className={classes.leftSide} gutterBottom>
                                        Artificial Intelligence Enabled Platform
                                    </Typography>
                                </Paper>
                            </Grid>
                            <Grid item sm={3}>
                                <Paper className={classes.featureCard}>
                                    <img className={classes.featureCardImage} src="/assets/images/incognito.png" alt="Incognito"/>
                                    <Typography variant={"body1"} className={classes.leftSide} gutterBottom>
                                        Full Anonymity During Chats and Discussions
                                    </Typography>
                                </Paper>
                            </Grid>
                            <Grid item sm={3}>
                                <Paper className={classes.featureCard}>
                                    <img className={classes.featureCardImage} src="/assets/images/fast-forward.png" alt="FF"/>
                                    <Typography variant={"body1"} className={classes.leftSide} gutterBottom>
                                        Super Fast Service with Minimal Intermediate Steps
                                    </Typography>
                                </Paper>
                            </Grid>
                            <Grid item sm={3}>
                                <Paper className={classes.featureCard}>
                                    <img className={classes.featureCardImage} src="/assets/images/support.png" alt="Support"/>
                                    <Typography variant={"body1"} className={classes.leftSide} gutterBottom>
                                        Available 24x7, 365 Days
                                    </Typography>
                                </Paper>
                            </Grid>
                        </Grid>
                        <Divider/>
                    </section>
                    {/*Why us*/}
                    <section>
                        <Grid container spacing={8}>
                            <Grid item xs>
                                <br/>
                                <Typography variant={"display2"} className={classes.typography} gutterBottom={true}>
                                    True Saviour's Unique Initiatives
                                </Typography>
                                <Divider style={{width: '40%',position: 'relative', left: '30%', height: 2}}/>
                            </Grid>
                        </Grid>
                        <div className={classes.toolbar}/>
                        <Grid container spacing={0}>
                            <Grid item xs>
                                <div className={classes.saviourImage}>
                                    <img src="/assets/images/placeholder.png" height="100%" width="100%" alt="Girl with Dandelion"/>
                                </div>
                            </Grid>
                            <Grid item xs>
                                <br/>
                                <br/>
                                <Typography className={classes.typography} variant={"display1"} gutterBottom>
                                    <u>Sufferer Will be Saviour - S.W.S</u>
                                </Typography>
                                <br/>
                                <br/>
                                <br/>
                                <br/>
                                <Typography className={classes.typography} variant={"body1"} style={{fontSize: 20, lineSpacing: 1.5, lineHeight: 2.5}} noWrap={false} gutterBottom>
                                    We here at True Saviour believe that a <strong>Sufferer</strong> can indeed transform into a <strong>Saviour</strong>
                                    Every Survivor has the potential to inspire countless others going through the same pain as they went through.
                                    The Suffers are just one click away from their Saviours - The Survivors who have a better
                                    understanding of the trauma they went through d how they overcame it.
                                    We truely believe that One Individual is capable of helping countless others, thus - Sufferer Will be the Saviour
                                </Typography>
                                <br/>
                            </Grid>
                        </Grid>
                    </section>
                    <Footer/>
                </div>
            </div>
        )
    }
}

export default withStyles(styles)(HomeContainer)


