import React, {Component} from 'react';
import {withStyles} from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import {themeConfig} from '../../theme';
import Grid from '@material-ui/core/Grid';
const styles = theme => ({
    footer: {
        position: 'relative',
        bottom: 0,
        background: themeConfig.palette.primary.dark,
        height: 'auto',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        flexDirection: 'column',
        borderRadius: 1,
        boxShadow: '0 0 2px 4px rgba(5,5,5,0.3)'
    },
    typo: {
        fontFamily: 'Raleway, sans-serif',
        textAlign: 'center',
        fontSize: 18,
        color: themeConfig.palette.primary.contrastText
    },
    links: {
        textTransform: 'none',
        color: themeConfig.palette.secondary.light,
        fontFamily: 'Raleway, sans-serif',
        textDecoration: 'none',
        '&:hover': {
            textTransform: 'underline',
            textDecoration: 'underline',
            cursor: 'pointer'
        }
    }
});

class Footer extends Component {

    render(){
        const {classes} = this.props;
        return(
          <div className={classes.footer}>
              <Grid container spacing={8}>
                  <Grid item sm={8} style={{paddingLeft: 100}}>
                      <br/>
                      <div style={{width: '75%'}}>
                          <Typography variant={"subheading"} gutterBottom style={{color: 'rgba(250,250,250,0.9)'}}>
                              About True Saviour
                          </Typography>
                          <Typography variant={"body1"} gutterBottom style={{color: themeConfig.palette.primary.contrastText, fontFamily: 'Raleway, sans-serif'}}>
                              We provide an online counselling and emotional support platform designed to foster mental wellness that is easily accessible anytime and anywhere. It anonymously connects you with the right expert consisting of counsellors, life coaches, career guides, psychotherapists, psychologists, and people with rich and deep life experience, who understand you and guide you through complete confidential individual sessions.
                          </Typography>
                          <br/>
                          <br/>
                      </div>
                  </Grid>
                  <Grid item sm={4}>
                      <br/>
                      <img src="/assets/images/chat.png" style={{margin: 'auto', display: 'block', color: '#fff'}}/>
                  </Grid>
              </Grid>
              <Grid container spacing={8}>
                  <Grid item sm style={{paddingLeft: 100}}>
                      <Typography variant={"caption"} gutterBottom>
                          <a href="#" className={classes.links}>Home</a> | <a className={classes.links} href="#">About</a> | <a className={classes.links} href="#">Contact</a> | <a className={classes.links} href="#">Sitemap</a>
                      </Typography>
                      <br/>
                  </Grid>
              </Grid>
          </div>
        );
    }
}


export default withStyles(styles)(Footer);


