import React, {Component} from 'react'
import Button from '@material-ui/core/Button'
import {withStyles} from '@material-ui/core/styles'

const styles = theme => ({
    btnStyle: {
        border: "1.25px solid #fff",
        color: "#fff",
        borderRadius: '20% 20% 20% 20% / 50% 50% 50% 50%',
        '&:hover': {
            textShadow: '1px 1px #000',
            boxShadow: '0 0 1px 1px rgba(255,255,255, 0.7), inset 0 0 1px 1px rgba(255,255,255, 0.7)'
        }
    }
});

class OutlineButton extends Component {


    render(){
        const {classes} = this.props;
        return(
            <Button variant={"outlined"} className={classes.btnStyle}>
                {this.props.children}
            </Button>
        )
    }
}

export default withStyles(styles)(OutlineButton);


